<template>
    <div class="book clearfix" @click.self="toggle" ref="bookBig">
        <div class="top clearfix" :class="{active:topActive}">
            <img class="fl" src="../assets/images/back.png" alt="" @click="toback">
            <div class="fl">{{eyeActive?eyeData.open:eyeData.close}}</div>
            <img class="fr" src="../assets/images/shudian.png" alt="" @click="isActive=!isActive" >
            <img class="fr" src="../assets/images/shoucang.png" alt="" >
        </div>
        <div class="right-box " :class="{'active':isActive}">
            <div @click="otherView({name:'Bookrack'})">去书架</div>
            <div @click="otherView({name:'Bookdetail',query:{id}})">书籍详情</div>
        </div>
        <div class="bookmsg-Aa" :class="{active:AaActive}">
            <div class="clearfix Aa-box">
                <div class="fl size">字号</div>
                <div class="fl down" @click="toggleFont(1)">-</div>
                <div class="fl num">{{fontSize}}</div>
                <div class="fl up" @click="toggleFont(2)">+</div>
                <div class="fl like">默认</div>
            </div>
            <div class="clearfix Aa-box">
                <div class="fl size">行距</div>
                <div class="fl down" @click="togglelineHeight(1)">-</div>
                <div class="fl num">{{lineHeight}}</div>
                <div class="fl up" @click="togglelineHeight(2)">+</div>
                <div class="fl like">默认</div>
            </div>
        </div>
        <div class="bookmsg-Aa" :class="{active:BgActive}">
            <div class="fl bg">背景</div>
            <div class="fl cire" v-for="(item,index) in BgColor" :key="index" :style="{'backgroundColor':item.color}" :class="{'active':item.isActive}" @click="toggleBg(item)">
                
            </div>
        </div>
        <div class="bookmsg-out">
            <div @click="toggle" class="bookmsg">
                <div>{{title}}</div>
                <div  ref="booka"></div>
                
            </div>
        </div>
        <div class="all" :class="{active:topActive}" >
            <div class="tgbook clearfix"> 
                <div class="fl" @click="changelink(chaptersIndex-1 < 0 ? 0 : chaptersIndex-1 )">上一章</div>
                <div class="fr" @click="changelink(chaptersIndex+1 > chaptersData.length-1 ? chaptersData.length-1 : chaptersIndex+1)">下一章</div>
            </div>
            <div class="all-list clearfix">
                
                <div class="all-list-one fl"  @click="catalog(index)" v-for="(item,index) in allData" :key="index">
                    <div class="all-list-img">
                        <img :src='item.icon' alt="" class="auto-img">
                    </div>
                    <div class="mulu">{{item.title}}</div>
                </div>
                
            </div>
        </div>
        <div class="catalog" :class="{active:catalogActive}">
            <div class="catalog-title">
                {{Bigtitle}}
            </div>
            <div class="catalog-new clearfix">
                <div class="fl">连载中56分钟前更新</div>
                <div class="fr" @click="down()">{{isDown?downData.yes:downData.no}}</div>
            </div>
            <div class="catalog-list">
                <div class="catalog-list-one one-text" v-for="(item,index) in chaptersData" :key="index" @click="changelink(index)" :class="{'active':item.active}">
                    {{item.title}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                fontSize:14,
                lineHeight:30,
                BgActive:true,
                isActive:true,
                AaActive:true,
                eyeActive:false,
                Bigtitle:"",
                id:"",
                topActive:false,
                catalogActive:false,
                allData:[
                    {
                        title:"目录",
                        icon:require("../assets/images/mulu.png")
                    },
                    {
                        title:"背景",
                        icon:require("../assets/images/beijing.png")
                    },
                    {
                        title:"设置",
                        icon:require("../assets/images/Aa.png")
                    },
                    {
                        title:"护眼",
                        icon:require("../assets/images/eye.png")
                    },
                ],
                eyeData:{
                    open:"护眼:开",
                    close:"护眼:关"
                },
                BgColor:[
                    {
                        color:"#fef8ea",
                        isActive:true
                    },
                    {
                        color:"#d3f0d2",
                        isActive:false
                    },
                    {
                        color:"#e8d0ce",
                        isActive:false
                    },
                    {
                        color:"#c0d8f0",
                        isActive:false
                    },
                ],
                chaptersIndex:null,
                chaptersData:[],
                book:[],
                title:"",
                isDown:true,
                downData:{
                    yes:"正序",
                    no:"倒序"
                },

            }
        },
        created(){
            this.id = this.$route.query.id;
            this.Bigtitle = this.$route.query.title;
            this.getRead(this.id);
        },
        methods:{
            // 切换字体大小
            toggleFont(e){
                if(e==1){
                    this.fontSize = this.fontSize-1 > 9 ?  this.fontSize-1 : 9
                    this.$refs.booka.style.fontSize = this.fontSize + "px"
                }else{
                    this.fontSize = this.fontSize+1
                    this.$refs.booka.style.fontSize = this.fontSize + "px"
                }
            },
            // 切换行距
            togglelineHeight(e){
                if(e==1){
                    this.lineHeight = this.lineHeight-1 > 20 ?  this.lineHeight-1 : 20
                    this.$refs.booka.style.lineHeight = this.lineHeight + "px"
                }else{
                    this.lineHeight = this.lineHeight+1
                    this.$refs.booka.style.lineHeight = this.lineHeight + "px"
                }
            },
            // 切换背景颜色
            toggleBg(item){
                for(let i = 0 ; i < this.BgColor.length ; i ++){
                    if(this.BgColor[i].isActive){
                        this.BgColor[i].isActive = false
                        break
                    }
                }
                item.isActive = true
                this.$refs.bookBig.style.backgroundColor = item.color
            },
            // 跳转页面
            otherView(e){
                this.$router.push(e)
            },
            //切换章节正反顺序
            down(){
                this.isDown = !this.isDown
                let arr = []
                for(let i = this.chaptersData.length-1 ; i >= 0 ; i-- ){
                    arr.push(this.chaptersData[i])
                }
                
                this.chaptersData = arr
            },
            //返回
            toback(){
                this.$router.go(-1)
            },
            // 弹出框的显示隐藏
            toggle(){
                if(!this.AaActive||!this.BgActive){
                    this.BgActive = true
                    this.AaActive = true
                }else{
                    if(this.catalogActive){
                        this.catalogActive = false 
                        return
                    }
                    this.isActive = true
                    this.topActive = !this.topActive
                }
            },
            //点击弹出框事件
            catalog(index){
                if(index == 0){
                    this.catalogActive = true 
                    this.topActive = false
                }else if(index == 2){
                    this.topActive = false
                    this.AaActive = false
                }else if(index == 1){
                    this.topActive = false
                    this.BgActive = false
                }else{
                    if(!this.eyeActive){
                        this.$refs.bookBig.style.backgroundColor = '#f1dbb6'
                        this.eyeActive = true
                    }else{
                        this.$refs.bookBig.style.backgroundColor = '#fef8ea'
                        this.eyeActive = false
                    }
                    
                }
            },
            //   获取书源
            getRead(id) {
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });
                this.axios({
                    method: "GET",
                    url: "/book-sources",
                    params: {
                    view: "summary",
                    book: id
                    }
                })
                .then(result => {
                    //关闭加载提示
                    this.$toast.clear();
                    
                    let sourceId = result.data[0]._id;
                    this.getChapter(sourceId);
                    })
                .catch(err => {
                    //关闭加载提示
                    this.$toast.clear();
                    });
            },
            // 获取章节列表
            getChapter(id) {
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });
                this.axios({
                    method: "GET",
                    url: `/book-chapters/${id}`
                })
                .then(result => {
                    //关闭加载提示56f8da09176d03ac1983f6cd
                    this.$toast.clear();
                    
                    this.chaptersData = result.data.chapters;
                    // this.chaptersData.active = false;
                    this.chaptersData.forEach(v => {
                        v.active = false;
                    })
                    this.chaptersData[0].active = true;
                    // 
                    let link = encodeURIComponent(result.data.chapters[0].link);
                    
                    this.getChapterDetail(link);
                    })
                    .catch(err => {
                    //关闭加载提示
                    this.$toast.clear();
                });
            },
            //转码link
            changelink(index){
                
                
                for(let i = 0 ; i < this.chaptersData.length ; i ++){
                    if(this.chaptersData[i].active){
                        this.chaptersData[i].active = false
                        break
                    }
                }
                this.chaptersData[index].active = true
                let link = encodeURIComponent(this.chaptersData[index].link);
                this.getChapterDetail(link);
                this.chaptersIndex = index 
            },
            //获取章节内容
            getChapterDetail(link) {
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });
                this.axios({
                    method: "GET",
                    url: `/chapters/${link}`
                })
                    .then(result => {
                        //关闭加载提示
                        this.$toast.clear();
                        
                    
                        this.book = result.data.chapter.cpContent.replace(
                            /\s\s\s/g,
                            "<br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"
                        );
                        
                        this.$refs.booka.innerHTML = this.book
                        this.title = result.data.chapter.title;
                    })
                    .catch(err => {
                    //关闭加载提示
                    this.$toast.clear();
                    });
                },
        }
    }
</script>

<style lang="less" scoped>
.bookmsg-Aa.active{
    display: none;
}
.bookmsg-Aa{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    // height: 100px;
    background-color: #f3ddb8;
    font-size: 16px;
    padding: 20px 10px;
    line-height: 20px;
    .bg{
        line-height: 50px;
        margin-left: 10px;
        color: #cf9734;
    }
    .cire{
        width: 50px;
        height: 50px;
        border-radius: 50%; 
        margin-left: 20px; 
    }
    .cire.active{
        border: 3px solid #cf9734;
    }
    .Aa-box{
        padding: 10px 0;
    }
    .size{
        color: #c5ab86;
        margin:0 30px;
    }
    .down{
        color: #997651;
        border: 1px solid #997651 ;
        padding: 0 20px;
        border-radius: 10px;
        font-size: 20px;
    }
    .num{
        color: #997651;
        margin: 0 30px;
    }
    .up{
        color: #997651;
        border: 1px solid #997651 ;
        padding: 0 20px;
        border-radius: 10px;
        font-size: 20px;
    }
    .like{
        color: #d3a647;
        margin-left: 30px;
    }
}
.right-box.active{
    display: none;
}
.right-box{
    position: absolute;
    right: 10px;
    top: 40px;
    width: 150px;
    // height: 80px;
    border-radius: 5px;
    color: #a1865b;
    font-size: 16px;
    text-align: center;
    background-color: #f1dbb6;
    border: 1px solid #9b7f58;
    >div{
        padding: 10px ;
    }
}
.bookmsg-out{
    width: 100%;
    margin-top: -60px;
    height: 617px;
    overflow: hidden;
    overflow-y: scroll;
}
.bookmsg-out::-webkit-scrollbar {
    display: none;
}
.bookmsg{
    font-size: 14px;
    line-height: 30px;
    width: 100%;
    padding: 20px;
    box-sizing: border-box;

    // margin-top: -30px;
}
.top{
    width: 100%;
    height: 20px;
    transition: opacity .15s,transform .15s,visibility .15s;
    transform: translateY(-100%);
    // opacity: 0;
    background-color: #f1dbb6;
    padding: 12px 0;
    line-height: 20px;
    >img{
        margin: 0 15px;
        padding-top: 3px;
    }
}
.top.active{
    transform: translateY(0);
    // opacity:.5;

}
.book{
    width: 100%;
    height: 667px;
    background-color:#fef8ea ;
    position: relative;
    overflow: hidden;
}
.all.active{
    transform: translateY(2%);
    box-shadow: 0px 5px 5px 5px #000;
}
.all{
    
    transition: opacity .15s,transform .15s,visibility .15s;
    transform: translateY(100%);
    width: 100%;
    position: absolute;
    bottom: 0px;
    background-color: #f1dbb6;
    .tgbook{
        
        padding: 10px 25px;
    }
    .all-list{
        
        .all-list-one{
            width: 30px;
            // height: 90px;
            text-align: center;
            padding: 20px 30px;
        }
        .all-list-img{
            width: 30px; 
        }
        .mulu{
            margin-top: 6px;
        }
    }
}
.catalog.active{
    transform: translateX(0);
    box-shadow: 1px 0px 10px 1px #000;
}
.catalog{
    transition: opacity .15s,transform .15s,visibility .15s;
    transform: translateX(-100%);
    width: 200px;
    height: 670px;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #e2f0fd;
    padding: 10px;
    
    .catalog-title{
        font-size: 18px;
    }
    .catalog-new{
        margin-top: 10px;
        border-bottom: 1px solid #ccc;
        padding-bottom: 5px;
        line-height: 25px;
        >.fl{
            color: #a8bfd0;
        }
        >.fr{
            color: #fed547;
            font-size: 14px;
        }
    }
    .catalog-list{
        height: 595px;
        font-size: 14px;
        color: #a8bfd0;
        overflow: hidden;
        overflow-y: scroll;
        
        .catalog-list-one{
            padding: 8px 0;
            border-bottom: 1px solid #ccc;
        }
        .catalog-list-one.active{
            color: #9c7854;
        }
    }
    .catalog-list::-webkit-scrollbar {
        display: none;
    }
}

</style>